.section-intro {
  background-image: url("/static/images/343.jpg");
  height: calc(100vh - 60px);
  padding: 30px;
  color: #fff; }

.section-contact {
  background-image: url("/static/images/space.jpg");
  background-size: cover;
  background-position: center center;
  height: calc(100vh - 60px);
  padding: 30px;
  color: #fff; }

.circle-nav-container {
  margin-left: 15vw; }

.circle-nav {
  display: flex;
  flex-direction: row; }
  .circle-nav svg {
    margin: 5px;
    cursor: pointer; }
    .circle-nav svg circle, .circle-nav svg line, .circle-nav svg text {
      transition: 0.5s; }
    .circle-nav svg text {
      font-size: 20px; }
    .circle-nav svg:hover .circle-highlight {
      stroke: #673ab7; }
    .circle-nav svg:hover circle {
      stroke-width: 40;
      stroke: #8bc34a; }
    .circle-nav svg:hover text {
      font-size: 30px; }
  .circle-nav .circle-line {
    stroke: white;
    stroke-width: 2; }
  .circle-nav .circle-text {
    font-family: 'Comfortaa', cursive; }
